<template>
  <main class="app">
    <h1>Тест по истории WW2</h1>

    <section class="quiz" v-if="!quizCompleted">
      <div class="quiz-info">
        <span class="question">{{ getCurrentQuestion.question }}</span>
        <span class="score">Правильных ответов {{ score }}/{{ questions.length }}</span>
      </div>

      <div class="options">
        <label
          v-for="(option, index) in getCurrentQuestion.options"
          :key="index"
          :class="`option ${
            getCurrentQuestion.selected == index
              ? index == getCurrentQuestion.answer
                ? 'correct'
                : 'wrong'
              : ''
          } ${
            getCurrentQuestion.selected != null &&
            index != getCurrentQuestion.selected
              ? 'disabled'
              : ''
          }`"
        >
          <input
            type="radio"
            :name="getCurrentQuestion.index"
            :value="index"
            v-model="getCurrentQuestion.selected"
            :disabled="getCurrentQuestion.selected"
            @change="SetAnswer"
          />
          <span>{{ option }}</span>
        </label>
      </div>
      <button @click="NextQuestion" :disabled="!getCurrentQuestion.selected">
        {{
          getCurrentQuestion.index == questions.length - 1
            ? "Завершить"
            : getCurrentQuestion.selected == null
            ? "Выберите ответ"
            : "Следующий вопрос"
        }}
      </button>
    </section>

    <section v-else>
      <h2>Вы закончили тест с результатом :</h2>
      <p>{{ percent }}% из 100</p>
    </section>
  </main>
</template>

<script>
import { ref, computed } from "vue";
export default {
  setup() {
    const questions = ref([
      {
        question: "Когда началась Вторая мировая война?",
        answer: 0,
        options: ["1 сентября 1939 г.", "17 февраля 1939. г", "22 июня 1941 г."],
        selected: null,
      },
      {
        question: "Какая страна первой подверглась нападению Германии?",
        answer: 2,
        options: [
          "Франция",
          "СССР",
          "Польша",
        ],
        selected: null,
      },
      {
        question: "Какие из перечисленных стран, являлись основными членами стран «Оси»?",
        answer: 1,
        options: [
          "Великобритания, Германия, СССР",
          "Германия, Италия, Япония",
          "Швеция, Норвегия, Австрия",
        ],
        selected: null,
      },
      {
        question: "Какие основные государства входили в антигитлеровскую коалицию?",
        answer: 2,
        options: [
          "США, Великобритания, Япония",
          "Китай, Япония, Франция",
          "Великобритания, СССР, США",
        ],
        selected: null,
      },
      {
        question: "Как звали лидера национальной фашистской партии, диктатора, возглавлявшего Италию как премьер-министр в 1922—1943 годах?",
        answer: 1,
        options: [
          "Иоахим фон Рииббентроп",
          "Бенито Муссолини",
          "Ферруччо Ламборгини",
        ],
        selected: null,
      },
      {
        question: "Кем являлся Генрих Гиммлер (1929—1945)?",
        answer: 0,
        options: [
          "Один из главных деятелей Третьего рейха, нацистской партии и рейхсфюрер СС, рейхсминистр внутренних дел Германии",
          "Немецкий лётчик-ас, считается наиболее результативным пилотом-истребителем за всю историю авиации.",
          "Верховный главнокомандующий вооружёнными силами Германии (с 19 декабря 1941) во Второй мировой войне.",
        ],
        selected: null,
      },
      {
        question: "В какой из перечисленных стран, итало-германская армия вела бои с вооруженными силами Великобритании и США?",
        answer: 0,
        options: [
          "Тунис",
          "Австралия",
          "Бразилия",
        ],
        selected: null,
      },
      {
        question: "В какой из перечисленных стран, итало-германская армия вела бои с вооруженными силами Великобритании и США?",
        answer: 0,
        options: [
          "Тунис",
          "Австралия",
          "Бразилия",
        ],
        selected: null,
      },
      {
        question: "Солдаты каких основных государств принимали участие в высадке в Нормандии?",
        answer: 2,
        options: [
          "Бразилия, Ирландия, Великобритания",
          "Великобритания, США, СССР",
          "Великобритания, Канада, США",
        ],
        selected: null,
      },
      {
        question: "Как называлась первая часть стратегической операции, и как назвалась сама стратегическая операция по высадке «Союзников» в Нормандии против Германии?",
        answer: 2,
        options: [
          "Уран, Цитадель",
          "Тайфун, Барбаросса",
          "Нептун, Оверлорд",
        ],
        selected: null,
      },
      {
        question: "Как называлась первая часть стратегической операции, и как назвалась сама стратегическая операция по высадке «Союзников» в Нормандии против Германии?",
        answer: 2,
        options: [
          "Уран, Цитадель",
          "Тайфун, Барбаросса",
          "Нептун, Оверлорд",
        ],
        selected: null,
      },
    ]);
    const quizCompleted = ref(false);
    const currentQuestion = ref(0);
    const score = computed(() => {
      let value = 0;
      questions.value.map(q => {
        if (q.selected != null && q.answer == q.selected) {
          console.log("correct");
          value++;
        }
      });
      return value;
    });
    const percent = computed(() => {
      let calculated = score.value
      let total = questions.value.length
      let result = calculated * 100 / total
      console.log(calculated)
      console.log(total)
      console.log(result)

      return result.toFixed()
    })
    const getCurrentQuestion = computed(() => {
      let question = questions.value[currentQuestion.value];
      question.index = currentQuestion.value;
      return question;
    });
    const SetAnswer = e => {
      questions.value[currentQuestion.value].selected = e.target.value;
      e.target.value = null;
    };
    const NextQuestion = () => {
      if (currentQuestion.value < questions.value.length - 1) {
        currentQuestion.value++;
        return;
      }

      quizCompleted.value = true;
    };

    return {
      questions,
      quizCompleted,
      currentQuestion,
      score,
      getCurrentQuestion,
      SetAnswer,
      NextQuestion,
      percent
    };
  },
};
</script>

